import { Component, OnInit,Input,ElementRef } from '@angular/core';
import {Question} from '../+suggestion/question.model';
import {JustificationService} from '../shared/justification.service';
declare var Spinner:any;
declare var $:any;

@Component({
  moduleId: module.id,
  selector: 'cyc-question',
  templateUrl: 'cycquestion.component.html',
  styleUrls: ['cycquestion.component.css'],
  providers: [JustificationService]
})
export class CycquestionComponent implements OnInit {

  private elementRef:ElementRef;

  @Input()
  private question:Question;
  @Input()
  private index:Number;
  @Input()
  private sessionId:string;
  private selectedQuestion:Question;
  private isProofViewOpen:boolean = false;
  private rationale:any;
  private isAnswerSelected:boolean = false;

  public startDraggingQuestion(e,question,index){
    e.dataTransfer.setData('custom',JSON.stringify(question));
    e.dataTransfer.setData('index',index+1);
    console.log(question);
  }
  public selectAnswer(answer){
    answer.isAnswerSelected = !answer.isAnswerSelected;
    console.log(answer);
  }
  constructor(private _justification:JustificationService,element:ElementRef) {
    this.elementRef = element;
  }

  ngOnInit() {
  }

    public showRationale(question) {
    console.log(question);
      this.isProofViewOpen = true;
    var elem = document.getElementById('spinner');
    var spinner = new Spinner({ radius: 60, width: 20, length: 80, lines: 15, className: 'loader', color: 'wheat' }).spin(elem);
    var ref = this;
   this._justification.fetchJustificationForConcept(question.index,this.sessionId)
                      .subscribe(data =>
                        // data => {
                        //   this.rationale = data;
                        //   $('#proofQuestionId').dialog();
                        // }
                        {
        this.rationale = data.proofs;
        spinner.stop();
        console.log(this.rationale);
        console.log("open the dialog");
        $("#proofQuestionId").dialog({
          modal: true,
          position: 'center',
          width: 1000,
          height: 500,
          title: "Rational for :" + question.nl,
          show: {
            effect: "blind",
            duration: 1000
          },
          hide: {
            effect: "explode",
            duration: 1000
          },
          close: function(event,ui) {
            ref.isProofViewOpen = false;
          }
        });
      }
      )

  }

}
